import { Row, Table } from 'antd';
import { Loading } from 'components/Loading';
import { IInvoice } from 'context/Invoice/types';
import * as React from 'react';
import { InjectedIntlProps, injectIntl } from 'react-intl';
import { Empty } from '.';

interface IProps extends InjectedIntlProps {
  rows?: any[];
  selectable?: boolean;
  className?: string;
  columns: any[];
  hasMore?: boolean;
  loadMore?: (more: any) => Promise<void>;
  onClickRow?: (invoice: IInvoice) => void;
}

interface IState {
  limit?: number;
  offset?: number;
}

class Common extends React.PureComponent<IProps, IState> {
  static defaultProps = {};

  state = {
    limit: 8,
    offset: 0,
  };

  render() {
    const { className, rows, hasMore, columns, intl } = this.props;

    return (
      <Row
        style={{
          width: '100%',
        }}
      >
        {rows && rows.length > 0 && (
          <Table
            key="table"
            className={className}
            pagination={false}
            rowKey="id"
            bordered={false}
            dataSource={rows}
            columns={columns}
          />
        )}
      </Row>
    );
  }
}

export default injectIntl(Common);
